<template>
	<credit-check @loadList="loadList" @loadNew="loadNew" :data="data" :nodeStatus="nodeStatus"></credit-check>
</template>
<script>
	import creditCheck from './creditcheck.vue'
	export default {
		data () {
			return {
				data:{},
				nodeStatus:1002
			}
		},
		components:{
			creditCheck
		},
		methods: {
			loadList: function (searchParams) {
				let _this = this;
				let params=searchParams.params;
				_this.$Message.config({
					top: 200
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if(searchParams.tab == 'credit'){//授信复审=>当前节点flowStatus为0的
					params.flowStatus=0;
					params.nodeStatus=_this.nodeStatus;
				}else if(searchParams.tab == 'history'){//授信复审审历史记录,增加任意loanId为请求历史记录
					params.nodeStatus=_this.nodeStatus;
					params.loanId=1;
				}
				this.$http.post('loanManage/loanCommon',params)
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Message.success({
								content: res.msg
							});
							if(res.data.list){
								_this.data=res.data;
							}
						} else {
							_this.$Message.error(res.msg);
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						});
					});
			},
			loadNew:function (searchParams) {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '获取新进件中...',
						duration: 0
					});
				}, 500);
				_this.$http.post('/ejie/checkNewOrder', {})
					.then(function (response) {
						var res = response.data;
						clearTimeout(timer);
						_this.$Message.destroy();
						if (res.code == 1) {//正常
							_this.$Notice.success({
								title:res.msg
							});
							_this.loadList(searchParams);
						} else {
							_this.$Message.error({
								content: res.msg
							});
						}
					})
					.catch(function (error) {
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.$Message.error({
							content: error
						});
					});
			}
		}
	}
</script>
<style>

</style>


